.CheckSpacePage {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-family: Arial, sans-serif;
  
    p {
      font-size: 16px;
      margin-bottom: 10px;
    }
  
    form {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 20px;
  
      label {
        font-size: 16px;
        margin-bottom: 10px;
      }
  
      input {
        width: 100%;
        padding: 10px;
        font-size: 16px;
        border: 1px solid #ccc;
        border-radius: 5px;
        margin-bottom: 10px;
      }
  
      button {
        padding: 0.5rem 1rem;
        font-size: 16px;
        background-color: #007bff;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s ease;
  
        &:hover {
          background-color: #0056b3;
        }
      }
    }
  
  .two-column-table {
    display: flex;
    justify-content: space-between;

    .column {
      width: 48%; /* 每列占总宽度的 48% */
      margin: 0 1%; /* 每列左右各 1% 的间距 */

      table {
        width: 100%;
        border-collapse: collapse;

        th, td {
          padding: 8px;
          text-align: left;
          border: 1px solid #ddd;
        }

        th {
          background-color: #f2f2f2;
        }
      }
    }

  .vertical-divider {
    width: 2px;
    background-color: #ddd;
    height: 100%;
  }
  
      tr:nth-child(even) {
        background-color: #f9f9f9;
      }
  
      tr:hover {
        background-color: #e3f2fd;
      }
    }
  
    .error {
      color: red;
      font-size: 16px;
      margin-top: 10px;
    }
  }